.btn-pri , .btn-sec{
  padding: 10px;
  font-size: 20px;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  text-transform: capitalize;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
}
.btn-pri{
  color: #ffffff;
  background: var(--main-color);
  border: 1px solid var(--main-color);
}
.btn-pri:hover{
  background: rgb(255, 255, 255);
  color: black;
}
.btn-sec{
  border: 1px solid rgb(170, 170, 170);
  color: black;
  background-color: rgb(255, 255, 255);
}
.btn-sec:hover{
  color: #ffffff;
  background:var(--main-color) ;
}
span.hide-text{

  animation: animate-logo 0.7s 1  forwards ease;
  -webkit-animation: animate-logo 0.7s 1  forwards ease;
}
span.show-text{
  animation: animate-logo2 0.7s 1  forwards ease;
  -webkit-animation: animate-logo2 0.7s 1  forwards ease;
}
span.hide-arabic{
  animation: animate-logo3 0.7s 1  forwards ease;
  -webkit-animation: animate-logo3 0.7s 1  forwards ease;
}
span.show-arabic{
  animation: animate-logo4 0.7s 1  forwards ease;
  -webkit-animation: animate-logo4 0.7s 1  forwards ease;
}
@keyframes animate-logo {
  from{
    opacity: 1;
    z-index: 1;
    left: 0px;
  }
  to{
    opacity: 0;
    z-index: -1;
    left: -110px;
  }
}
@keyframes animate-logo2 {
  from{
    opacity: 0;
    z-index: -1;
    left: -110px;
  }
  to{
    opacity: 1;
    z-index: 1;
    left: 0px;
  }
}
@keyframes animate-logo3 {
  from{
    opacity: 0;
    z-index: -1;
    right: 0px;
  }
  to{
    opacity: 1;
    z-index: 1;
    right: -110px;
  }
}
@keyframes animate-logo4 {
  from{
    opacity: 0;
    z-index: -1;
    right: -110px;
  }
  to{
    opacity: 1;
    z-index: 1;
    right: 0px;
  }
}
body{
