.landing{
  height: calc(100vh - 100px);
    position: relative;
    z-index:1 ;
    background: url('./static/mosque-6231915_1280.jpg') ;
    background-size: cover;
    background-position: center center;
}

.landing.bg-change{
  position: relative;
  z-index:1 ;
  background: url('./static/mosque3.png') ;
  background-size: cover;
  background-position: center center;
  animation: landing-bg-change 0.3s   1 forwards  ease;
  -webkit-animation: landing-bg-change 0.3s   1 forwards  ease;
}

.landing::after  {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.7);
}
