.nav-in-small{
  width: 0px;
  /* box-shadow: 0 0 10px rgb(214, 214, 214); */
  height: 0px;
  transition: 0.3s;
  overflow: hidden;
  position: absolute;
  background-color: #ffffff;
  top: 80px;
  right: 30px;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.nav-in-small-arabic{
  width: 0px;
  /* box-shadow: 0 0 10px rgb(214, 214, 214); */
  height: 0px;
  transition: 0.3s;
  overflow: hidden;
  position: absolute;
  background-color: #ffffff;
  top: 80px;
  right: -160px;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
