/* teachers */
.teachers ,.Programms,.pricing{
  position: relative;
  z-index: 0;
}
.teachers::before , .Programms::before , .pricing::before{
  position: absolute;
  content: '';
  background-image: linear-gradient(to left , rgb(230, 230, 230)  ,rgb(253, 253, 253) );
  width: 100%;
  height: 100%; 
  z-index: -1;
}
.teachers .content >div , .pricing .content >div{
  position: relative;
  z-index: 0;
  overflow: hidden;
  padding: 10px;
  background-color: #ffffff;
