  visibility: visible;
}

.free-trail{
  position: fixed;
  z-index: 20000;
  height: 70px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  background-color: var(--main-color);
  width: 100%;
  animation: animate-free-trail 0.3s 6s  1 forwards ease;
  -webkit-animation: animate-free-trail 0.3s 8s  1 forwards ease;
}
@keyframes animate-free-trail {
  from{
    /* height: 0; */
    opacity: 0;
    visibility: hidden;
  }
  to{
    /* height: 100px; */
    visibility: visible;
    opacity: 1;
  }
}
@keyframes animate-free-trail-hide {
  from{
    visibility: visible;
    opacity: 1;
  }
  to{
    opacity: 0;
    visibility: hidden;
  }
}
.free-trail.hide{
  animation: animate-free-trail-hide 0.5s  1 forwards ease;
  -webkit-animation: animate-free-trail-hide 0.5s  1 forwards ease;
}

.free-trail button{
  background-color: #1f436d;
}

